import React from "react";
import { connect } from "react-redux";
import { getCategoryList } from "../../service/thunk/thunkCreator";
import { bindActionCreators } from "redux";
import { useEffect } from "react";
import { Wrapper } from "../hoc/category_hoc";
import "./style.css";
import { withRouter } from "react-router-dom";

const CategoryListPhone = ({ category, getCategoryList, history }) => {
  useEffect(() => {
    getCategoryList();
  }, []);

  return (
    <div className="row">
      {category.map((item, index) => (
        <div className="col-4" key={index}>
          <div
            className="category-phone-wrapper"
            onClick={() => history.push(`/products/${item.name}`)}
          >
            <img src={item.image} className="category-image" />
            <div className="category-display-phone">
              <span className="category-list-item-phone">{item.name}</span>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

const CategoryListDesktop = ({ category, getCategoryList, history }) => {
  useEffect(() => {
    getCategoryList();
  }, []);

  return (
    <ul className="list-group">
      <h4 className="list-group-item category-list-item">Категории</h4>
      {category.map((item, index) => (
        <li
          className="list-group-item category-list-item"
          key={index}
          onClick={() => history.push(`${item.name}`)}
        >
          {item.name}
          <span className="category-item-count">{item.count}</span>
        </li>
      ))}
    </ul>
  );
};

const putStateToProps = (state) => {
  return {
    category: state.ProductReducer.category,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ getCategoryList }, dispatch);
};

export const CategoryDesktop = connect(
  putStateToProps,
  mapDispatchToProps
)(Wrapper(CategoryListDesktop));

export const CategoryPhone = connect(
  putStateToProps,
  mapDispatchToProps
)(Wrapper(CategoryListPhone));
