import React from "react";
import "./style.css";
import { Link } from "react-router-dom";

const NavBar = () => {
  return (
    <nav className="custom-nav">
      <div className="title-for-shop">
        <Link to="/">
          <h3>Market</h3>
        </Link>
      </div>
    </nav>
  );
};

export default NavBar;
