import React from "react";
import { connect } from "react-redux";
import {
  getProductList,
  getSubCategoryList,
} from "../../service/thunk/thunkCreator";
import { bindActionCreators } from "redux";
import { useEffect, useState } from "react";
import ProductItem from "./product-item";
import searchicon from "./search.png";

import "./style.css";
const ProductList = ({
  products,
  getProductList,
  loading,
  getSubCategoryList,
  subcategory,
  filter,
}) => {
  let [inputFilter, setInput] = useState("");

  useEffect(() => {
    getProductList(filter.filter);
    return () => console.log("disable");
  }, [filter]);

  useEffect(() => {
    getSubCategoryList(products);
  }, [products]);

  const search = (value) => {
    if (value != "") {
      getProductList(filter.filter, value);
    } else {
      return;
    }
  };

  if (!loading) {
    return (
      <div className="row product-block">
        <div className="col-12 p-0">
          <input
            className="form-control m-3 ml-3 search"
            placeholder="Поиск..."
            onChange={(e) => setInput(e.target.value)}
          />
          <div onClick={() => search(inputFilter)} className="search-button">
            <img src={searchicon} className="search-icon" />
          </div>
        </div>
        <div className="col-12">
          {products.length == 1 ? (
            <div className="product-group">
              <div className="product-header">
                <h3>
                  {products[0].subcategory[0].name}
                  <span className="product-edit"></span>
                </h3>
              </div>
              <div className="row">
                <ProductItem product={products[0]} />
              </div>
            </div>
          ) : (
            subcategory.map((item, index) => (
              <div className="product-group" key={index}>
                <div className="product-header">
                  <h3>
                    {item.name}
                    <span className="product-edit">{item.count}</span>
                  </h3>
                </div>
                <div className="row">
                  {products
                    .filter((unfiltered) =>
                      unfiltered.subcategory
                        .map((sub) => sub.name)
                        .indexOf(item.name) > -1
                        ? true
                        : false
                    )
                    .map((product, index) => (
                      <ProductItem product={product} key={index} />
                    ))}
                </div>
              </div>
            ))
          )}
        </div>
      </div>
    );
  } else {
    return <div className="spinner-border text-dark"></div>;
  }
};

const putStateToProps = (state) => {
  return {
    products: state.ProductReducer.products,
    loading: state.GeneralReducer.loading,
    subcategory: state.ProductReducer.subcategory,
  };
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({ getProductList, getSubCategoryList }, dispatch);
};

export default connect(putStateToProps, mapDispatchToProps)(ProductList);
