export const FETCHING_DATA = () => {
  return { type: "FETCHING_DATA" };
};

export const FETCHING_DATA_SUCCESS = () => {
  return { type: "FETCHING_DATA_SUCCESS" };
};

export const FETCHING_DATA_FAIL = (error) => {
  return { type: "FETCHING_DATA_FAIL", payload: error };
};

export const GET_PRODUCT_LIST = (data) => {
  return { type: "GET_PRODUCT_LIST", payload: data };
};

export const GET_CATEGORY_LIST = (data) => {
  return { type: "GET_CATEGORY_LIST", payload: data };
};

export const GET_SUBCATEGORY = (data) => {
  return { type: "GET_SUBCATEGORY", payload: data };
};

export const GET_CART = () => {
  return { type: "GET_CART" };
};
