import React from "react";
import MainPage from "./mainpage/mainpage";
import MainPagePhone from "./mainpage/mainpage-phone";
import NavBar from "./navbar/navbar";

const App = () => {
  if (window.screen.width <= 600) {
    return (
      <div>
        <NavBar />
        <MainPagePhone />
      </div>
    );
  }
  return (
    <div>
      <NavBar />
      <MainPage />
    </div>
  );
};

export default App;
