const InitialState = {
  has_error: false,
  error: "",
  loading: true,
};

const GeneralReducer = (state = InitialState, action) => {
  switch (action.type) {
    case "FETCHING_DATA":
      return { ...state, loading: true, has_error: false };
    case "FETCHING_DATA_SUCCESS":
      return { ...state, loading: false, has_error: false };
    case "FETCHING_DATA_FAIL":
      return {
        ...state,
        loading: false,
        has_error: true,
        error: action.payload,
      };
    default:
      return state;
  }
};

export default GeneralReducer;
