import React from "react";
import "./style.css";
import plus from "./plus-icon.png";
import info from "./info.png";
import { useState } from "react";

const ProductItem = ({ product }) => {
  const [showMe, setShown] = useState(false);

  const showInfo = (e) => {
    setShown(!showMe);
  };

  return (
    <div className="product col-xl-4 col-sm-7 col-lg-6 col-6 ">
      <div className={showMe ? "info-block d-block p-2" : "info-block d-none"}>
        <p>{product.description}</p>
      </div>
      <div className="product-area">
        <img src={product.images[0].image} className="product-image" />
        <img
          src={info}
          className="info-product"
          onMouseEnter={(e) => showInfo(e)}
          onMouseLeave={(e) => showInfo(e)}
        />
        <h6>{product.name}</h6>
        <span className="product-edit" style={{ fontSize: "16px" }}>
          {product.characteristics[0].value} {product.characteristics[0].suffix}
        </span>
        <div className="price-block">
          <h4>{product.price}Р</h4>
          <div className="add-product">
            <img src={plus} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductItem;
